
/* Basic styles for the header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color:pink;
  border-bottom: 1px solid #ddd;
}

.logo img {
  max-height: 80px;
}

.nav ul {
  display: flex;
  list-style: none;
  padding: 50px;
  margin: 0;
  font-size: 16.7px;
  
}

.nav ul li {
  margin-right: 50px;
}

.nav ul li a {
  text-decoration: none;
  color: #000;
  font-weight: bold;
  
}

.user-controls {
  display: flex;
  align-items: center;
}

.user-controls  {
  margin-left: 10px;
  font-size: 30px;
  
}

.logo1{

margin-top: -5px;
}

.logo2{
  margin-left: 133px;
  margin-top: -20px;
}

.leader{
  color:rgb(51, 7, 80);
  font-size: 50px;
  font-style:italic;
}


/* Responsive Design */
@media screen and (max-width: 768px) {
  .nav ul {
    flex-direction: column;
    display: none;
  }

  .nav ul.show {
    display: flex;
  }

  .user-controls {
    flex-direction: column;
    align-items: flex-end;
  }
}



.basic-backtest {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.title {
  font-size: 24px;
  font-weight: bold;
}

.slippage {
  display: flex;
  align-items: center;
}

.slippage label {
  margin-right: 8px;
  font-size: 19px;
  

}

.slippage select {
  font-size: 16px;
  padding: 4px 8px;
}

@media (max-width: 600px) {
  .basic-backtest {
    flex-direction: column;
    align-items: flex-start;
  }

  .slippage {
    margin-top: 10px;
  }
}






.leaderboard-container {
  width: 100%;
  padding: 20px;
   box-sizing: border-box;
   
}

.leaderboard-container h1 {
  text-align: center;
  margin-bottom: 20px;
  font-weight: 500;
  
  
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  margin: 0 auto;
  font-size: 23px;
}

.leaderboard-table th, .leaderboard-table td {
  border: 1px solid palevioletred;
  padding: 8px;
  text-align: center;
}

.leaderboard-table th {
  background-color: rgb(243, 216, 220);
  font-weight: bold;
}

.leaderboard-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.leaderboard-table tr:hover {
  background-color: #ddd;
}

.leaderboard-table a {
  color: blue;
  text-decoration: none;
}

.leaderboard-table a:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .leaderboard-table, .leaderboard-table th, .leaderboard-table td {
    display: block;
    width: 100%;
  }
  
  .leaderboard-table thead {
    display: none;
  }
  
  .leaderboard-table tr {
    margin-bottom: 15px;
  }
  
  .leaderboard-table td {
    text-align: right;
    padding-left: 50%;
    position: relative;
  }
  
  .leaderboard-table td::before {
    content: attr(data-label);
    position: absolute;
    left: 10px;
    width: calc(50% - 20px);
    text-align: left;
    font-weight: bold;
  }
}
